* {
  box-sizing: border-box;
}

body {
  position: relative;
  max-width: 900px;
  margin-left: auto;
  margin-right: auto;

  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #dddddd44;
  color: #212121;
  line-height: 1.5;
}

.search-form{
padding-right: 15px;
}

.home{
color: #d8edc8;
padding-left: 15px;
}

ul{
margin: 0px;
  padding:0px;
}

li{
  padding: 10px;
  margin: 10px;
border: 1px solid grey;
border-radius: 10px;
list-style: none;
}

p{
margin: 0;
font-size: 300%;
}

img {
  display: block;
  max-width: 100%;
}

button{
  margin-left: auto;
  margin-right: auto;
  height: 30px;
padding:1px 7px;
align-items: center;
color: #d8edc8;
background-color: rgb(86, 31, 138);
border: 0.3px solid rgb(70, 6, 6);
border-radius: 4px;
}

.button > .spinner.is-hidden{
display: none;
}

.button.is-hidden{
display: none;
}

.div-a-center{
  display: flex;
  padding: 5px;
  align-items: center;
  flex-wrap: wrap;
  justify-content:space-between;
  border-radius: 5px;
  background-color: #642bbb;
}

.js-articles-container{
display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
}

.photo-card{
  margin: 5px;
  padding: 5px;
  border-radius: 10px;
    flex-wrap: wrap;
display: flex;
max-width: 20%;

background-color: rgb(255, 255, 255);
}

.photo-card:hover{
box-shadow: 1px 2px 10px  #b9b4b4;
}

.info{
  display: flex;
  width: 100%;
  padding: 5px;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
border-radius: 5px;
background-color: rgba(204, 164, 226, 0.39);
font-size: 3px;
font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif
};





